.movie-card {
  &__link {
    color: inherit;
    font-weight: 500;
font-size: 12px;
line-height: 1.33;
@media screen and (min-width: 1280px) {
    font-size: 20px;
    line-height: 1.2;
  }
  }

  &__holder {
    width: 100%;
    height: 402px;
    border-radius: 5px;
    overflow: hidden;
    margin-bottom: 10px;

    @media screen and (min-width: 768px) {
      height: 455px;
      margin-bottom: 8px;
    }

    @media screen and (min-width: 1280px) {
      height: 574px;
      margin-bottom: 12px;
    }
  }

  &__img {
    object-fit: cover;
  }

  &__title{
text-transform: uppercase;
}

  &__genre {   
    color: #B92F2C;    
  }

//   &__date {
    
//   }
}
